import { useContext, useEffect } from "react";
import { AppContext } from "../../App";
import { withAuthRedirect } from "../../hoc/withAuthRedirect";
import styles from "./Users.module.scss";
import UsersHeader from "./UsersHeader/UsersHeader";
import UsersListContainer from "./UsersList/UsersListContainer";
import Helmet from "react-helmet";

const Users = () => {
    const appContext = useContext(AppContext);
    const docTitle = appContext.currentLanguage === "eng" ? "Search | Feelanet" : appContext.currentLanguage === "ru" && "Поиск | Feelanet";

    useEffect(() => {
        document.title = docTitle;
    }, [appContext.currentLanguage]);

    return (
        <div className={styles.wrap}>
            <Helmet title={docTitle} />
            <UsersHeader />
            <UsersListContainer />
        </div>
    );
};

let AuthRedirectComponent = withAuthRedirect(Users);
export default AuthRedirectComponent;
